<script>
import { timeFormat } from '../../utils'
import { setTimeout } from 'timers';
export default {
  props: {
    tableSort: {
      type: Function
    },
    hideTableLabel: {
      type: Array,
      default() { return [] }
    },
    bind: {
      type: Object,
      required: true,
      default: () => ({ rowKey: 'id' })
    },
    on: {
      type: Object,
      default() { return {} }
    },
    columns: {
      type: Array,
      default() { return [] }
    },
    data: {
      type: Array,
      default() {
        return []
      }
    }
  },
  render() {
    const { data, tableSort, bind, bind: { rowKey } } = this
    const { getColumns, handlerSelect } = this
    if (!rowKey) {
      console.error('bind.rowKey不能为空')
    }
    return <el-table
      class='my-table'
      data={data}
      {...bind}
      on-select={handlerSelect}
      on-select-all={handlerSelect}
      row-key={rowKey}
      ref="table"
    >
      {getColumns(this.columns)}
      {tableSort && <el-table-column
        width="60"
        label="">
        <el-button plain circle icon="el-icon-sort" class="sortableHanlder"></el-button>
      </el-table-column>}
    </el-table>
  },
  mounted() {
    this.setTableSort()
  },
  methods: {
    setTableSort() {
      const { tableSort, data } = this
      if (tableSort) {
        let disabled = false
        import('sortablejs').then(({ Sortable }) => {
          const el = document.querySelectorAll('.el-table__body-wrapper > table > tbody')[0];
          Sortable.create(el, {
            animation: 150,
            dragClass: "expand-sortable-drag",
            handle: ".sortableHanlder",
            filter() {
              return disabled
            },
            onEnd: (evt) => {
              disabled = true
              this.$emit('sort', evt, () => {
                disabled = false
                let oldRow = data.splice(evt.oldIndex, 1)
                data.splice(evt.newIndex, 0, oldRow[0])
                setTimeout(() => {
                  this.$refs.table.doLayout()
                }, 0)
              })
            }
          })
        })
      }
    },
    handlerSelect(selection) {
      this.$emit('select', selection)
    },
    getColumns(arr) {
      const h = this.$createElement
      return arr.filter(it => !~this.hideTableLabel.indexOf(it.label)).map(it => {
        return h('el-table-column', {
          key: it.label,
          props: {
            align: this.getColumnsAlign(it),
            label: it.label,
            type: it.type,
            prop: it.prop,
            ...it.bind
          },
          on: it.on,
          scopedSlots: {
            default: this.getColumnsScopedSlots(it)
          }
        }, it.child && this.getColumns(it.child))
      })
    },
    getColumnsScopedSlots(it) {
      if (it.render) {
        return function (props) { return it.render(props) }
      }
      if (it.type === 'time') {
        return function (props) {
          return timeFormat(props.row[it.prop], it.format)
        }
      }
      if (it.type === 'image') {
        return function (props) { return <img class="table-img" src={props.row[it.prop]} /> }
      }
      if (it.type === 'color') {
        return function (props) { return <div class="table-color" style={{ backgroundColor: props.row[it.prop] }}></div> }
      }
      if (it.type === 'audio') {
        return function (props) { return <audio class="table-audio" controls="controls" src={props.row[it.prop]}></audio> }
      }
    },
    getColumnsAlign(col) {
      if (['selection', 'image', 'color', 'audio'].find(it => it === col.type)) {
        return 'center'
      }
    }
  }
}
</script>
<style lang="less">
.elpand-table {
  .my-table {
    margin-top: 12px;
    .expand-sortable-drag {
      opacity: 0;
    }
    .table-img {
      max-width: 120px;
      max-height: 120px;
      min-width: 38px;
      min-height: 38px;
    }
    .table-color {
      display: inline-block;
      width: 60px;
      height: 28px;
    }
    .table-audio {
      width: 270px;
    }
  }
}
</style>